class Check {}
Object.assign(Check.prototype, {
  Name(value) {
    if (!value) {
      return ''
    }
    if (!new RegExp('^[a-zA-Z0-9_\u4e00-\u9fa5\\s]+$').test(value)) {
      return ''
    }
    if (/(^_)|(__)|(_+$)/.test(value)) {
      return "\t'_'"
    }
    if (/^\d+$/.test(value)) {
      return ''
    }
  },
  Mobile(value) {
    if (!value) {
      return ''
    }
    if (!/^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/.test(value)) {
      return ''
    }
  },
  Email(value) {
    if (!value) {
      return ''
    }
    if (!/\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/.test(value)) {
      return ''
    }
  },
  Password(value) {
    if (!value) {
      return ''
    }
    if (value.length > 12 || value.length < 6) {
      return '6~12'
    }
  }
})
export default Check
