<template>
  <div>
    <bread :msg1="''" :msg2="''"></bread>
    <el-card
      class="box-card"
      :body-style="{ padding: '20px' }"
      :fullscreen="true"
    >
      <el-button type="primary" @click="dialogVisibleAdd = true"
        ></el-button
      >
      <el-table :data="tableData" style="width: 100%" border fit>
        <el-table-column type="expand" label="">
          <template slot-scope="list">
            <el-form label-position="left" inline>
              <el-row
                v-for="item in list.row.children"
                :key="item.authName"
                class="firstRows"
              >
                <el-col :span="5">
                  <el-tag effect="dark" closable type="primary">
                    {{ item.authName }} </el-tag
                  ><i
                    style="margin:0  10px"
                    class="el-icon-caret-right"
                  ></i></el-col
                ><el-col :span="19">
                  <!--  -->
                  <el-row
                    class="secondRows"
                    style=""
                    v-for="(item2, index2) in item.children"
                    :key="index2"
                  >
                    <el-col :span="5">
                      <div>
                        <el-tag effect="dark" closable type="success">
                          {{ item2.authName }}
                        </el-tag>
                        <i
                          style="margin:0  10px"
                          class="el-icon-caret-right"
                        ></i>
                      </div>
                    </el-col>
                    <el-col :span="19">
                      <!--  -->
                      <el-tag
                        effect="dark"
                        style="margin:  10px;"
                        v-for="(item3, index3) in item2.children"
                        :key="index3"
                        closable
                        type="warning"
                      >
                        {{ item3.authName }}
                      </el-tag>
                    </el-col>
                  </el-row>
                </el-col>
              </el-row>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column label="#" type="index" width="50px"> </el-table-column>
        <el-table-column label="" prop="roleName"> </el-table-column>
        <el-table-column label="" prop="roleDesc"> </el-table-column>
        <el-table-column label="">
          <template slot-scope="scoped">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="edit(scoped.row)"
            >
            </el-button>
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="del(scoped.row.id)"
            >
            </el-button>
            <el-button
              size="mini"
              type="warning"
              icon="el-icon-setting"
              @click="assign(scoped.row)"
            >
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisibleTree" width="50%">
      <!-- default-checked-keys:  key  -->
      <el-tree
        :data="list"
        show-checkbox
        :default-expand-all="true"
        node-key="id"
        ref="tree"
        :props="defaultProps"
        :label="list.authName"
        :default-checked-keys="this.defaultArr"
      >
      </el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisibleTree = false"> </el-button>
        <el-button type="primary" @click="getCurrentKey">
           
        </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <!--  dialog  -->
    <el-dialog
      title=""
      :visible.sync="dialogVisibleEdit"
      width="50%"
      :before-close="handleCloseEdit"
    >
      <el-form
        :model="formEdit"
        ref="editForm"
        :rules="rules"
        label-width="80px"
        :show-message="true"
      >
        <el-form-item label="" prop="roleName">
          <el-input v-model="formEdit.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="formEdit.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisibleEdit = false"> </el-button>
        <el-button type="primary" @click="editEnter('editForm')"
          > </el-button
        >
      </span>
    </el-dialog>
    <!--  dialog  -->
    <el-dialog
      title=""
      :visible.sync="dialogVisibleAdd"
      width="50%"
      :before-close="handleCloseAdd"
    >
      <el-form
        :model="formAdd"
        ref="addForm"
        :rules="rules"
        label-width="80px"
        :show-message="true"
      >
        <el-form-item label="" prop="roleName">
          <el-input v-model="formAdd.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="formAdd.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisibleAdd = false"> </el-button>
        <el-button type="primary" @click="addEnter('addForm')"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Check from '../../check.js'
const check = new Check()
export default {
  data () {
    // 
    var checkUsername = (rule, value, callback) => {
      const msg = check.Name(value)
      callback(msg && new Error(msg))
    }
    return {
      tableData: [],
      list: [],
      dialogVisibleTree: false,
      dialogVisibleEdit: false,
      dialogVisibleAdd: false, // 
      defaultProps: {
        children: 'children',
        label: 'authName'
      },
      formEdit: { roleDesc: '', roleName: '' },
      formAdd: { roleDesc: '', roleName: '' },
      defaultArr: [],
      rid: '',
      rules: {
        // 
        roleName: [{ validator: checkUsername, trigger: 'blur' }],
        roleDesc: [{ validator: checkUsername, trigger: 'blur' }]
      }
    }
  },
  methods: {
    /*  */
    async getList () {
      const res = await this.$axios.get('/roles')
      if (res.data.meta.status === 200) {
        this.tableData = res.data.data
      } else {
        this.$message({ message: '', type: 'error' })
      }
    },
    async getTree () {
      // 
      const res = await this.$axios.get('/rights/tree')
      if (res.data.meta.status === 200) {
        this.list = res.data.data
        this.dialogVisibleTree = true
      } else {
        this.$message({ message: '', type: 'error' })
      }
    },
    // id
    async addId (arr) {
      // 
      const promise = new Promise((resolve, reject) => {
        for (let i = 0; i < arr.length; i++) {
          if (!arr[i].children) {
            for (var j = 0; j < arr.length; j++) {
              this.defaultArr.push(arr[j].id)
            }
            return resolve()
          }
          this.addId(arr[i].children)
        }
      })
      return promise
    },
    /*  */
    assign (obj) {
      /* obj  */
      this.rid = obj.id
      this.getTree()
      // obj
      this.defaultArr = []
      this.addId(obj.children)
    },
    /*  */
    async getCurrentKey () {
      this.dialogVisibleTree = false
      const rids = this.$refs.tree.getCheckedKeys()
      const arr = this.$refs.tree.getHalfCheckedKeys()
      rids.push(...arr)
      const res = await this.$axios.post(`/roles/${this.rid}/rights`, {
        rids: rids.join(',')
      })
      if (res.data.meta.status === 200) {
        this.getList()
        this.$message({ message: '', type: 'success' })
      } else {
        this.$message({ message: '', type: 'error' })
      }
    },
    /*  */
    handleCloseEdit (done) {
      this.$confirm('')
        .then(_ => {
          done()
        })
        .catch(_ => {})
    },
    edit (obj) {
      this.rid = obj.id
      this.formEdit.roleDesc = obj.roleDesc
      this.formEdit.roleName = obj.roleName
      this.dialogVisibleEdit = true
    },
    editEnter (editForm) {
      this.$refs.editForm.validate(async valid => {
        if (valid) {
          // 
          const res = await this.$axios.put('/roles/' + this.rid, this.formEdit)
          if (res.data.meta.status === 200) {
            this.$message({ message: '', type: 'success' })
          } else {
            this.$message({ message: '', type: 'error' })
          }
        } else {
          this.$message({ message: '', type: 'error' })
        }
        this.dialogVisibleEdit = false
        this.getList()
      })
    },
    // 
    del (id) {
      this.$confirm(', ?', '', {
        cancelButtonText: '',
        confirmButtonText: '  ',
        type: 'warning',
        center: true
      })
        .then(async () => {
          const res = await this.$axios.delete('/roles/' + id)
          if (res.data.meta.status === 200) {
            this.getList()
            this.$message({ type: 'success', message: '!' })
          } else {
            this.$message({ type: 'error', message: '!' })
          }
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    // 
    handleCloseAdd (done) {
      this.$confirm('')
        .then(_ => {
          done()
        })
        .catch(_ => {})
      this.$refs.addForm.resetFields()
    },
    addEnter (addForm) {
      this.$refs[addForm].validate(async valid => {
        if (valid) {
          // 
          const res = await this.$axios.post('/roles', this.formAdd)
          if (res.data.meta.status === 201) {
            this.getList()
            this.$message({ message: '', type: 'success' })
          } else {
            this.$message({ message: '', type: 'error' })
          }
        } else {
          this.$message({ message: '', type: 'error' })
        }
        this.dialogVisibleAdd = false
      })
    }
  },
  created () {
    this.getList()
  }
}
</script>

<style lang="less" scoped>
.el-table {
  margin-top: 15px;
  /deep/.cell {
    font-size: 12px;
  }
}
.firstRows {
  &:nth-child(1) {
    border-top: 1px solid #ccc;
  }
  border: 1px solid #ccc;
  border-top: 0;
  display: flex;
  align-items: center;
  text-align: center;
  .secondRows {
    padding: 10px 0;
    display: flex;
    align-items: center;
    border: 1px solid #ccc;
    border-bottom: 0;
    &:nth-child(1) {
      border-top: 0;
    }
    .el-col-19 {
      text-align: left;
    }
  }
}
</style>
